<template>
  <div ref="calContainer" style="width: 100%; height: 100vh; overflow: auto"></div>
</template>

<script setup>
import { ref, onMounted } from 'vue'

const calContainer = ref(null)

const namespace = '15min'
const calLink = 'saad-dev-ginzwb/15min'
const layout = 'month_view'

onMounted(() => {
  // Wait until Cal and namespace are ready
  const waitForCal = () => {
    if (window.Cal) {
      if (!window.Cal.ns || !window.Cal.ns[namespace]) {
        // Initialize namespace
        window.Cal('init', namespace, { origin: 'https://app.cal.com' })
      }

      const checkNamespace = () => {
        if (window.Cal.ns && window.Cal.ns[namespace]) {
          // Initialize inline embed
          window.Cal.ns[namespace]('inline', {
            elementOrSelector: calContainer.value,
            config: { layout },
            calLink
          })

          // Optional UI settings
          window.Cal.ns[namespace]('ui', { hideEventTypeDetails: false, layout })
        } else {
          setTimeout(checkNamespace, 100) // Retry until namespace exists
        }
      }

      checkNamespace()
    } else {
      setTimeout(waitForCal, 100) // Retry until Cal is loaded
    }
  }

  waitForCal()
})
</script>
